export class Text{
    constructor(){
        this.canvas = document.createElement("canvas");
        this.ctx = this.canvas.getContext("2d");
    }

    setText(str, density, stageWidth, stageHeight){
        this.canvas.width = stageWidth;
        this.canvas.height = stageHeight;

        const myText = str;
        const fontWidth = 700;
        const fontSize = 500;
        const fontName = 'Hind';

        this.ctx.clearRect(0, 0, stageWidth, stageHeight);
        this.ctx.font = `${fontWidth} ${fontSize}px ${fontName}`;
        this.ctx.textBaseline = 'middle';
        this.ctx.fillStyle = 'rgba(0, 0, 0, 0.3)';

        const fontInfo = this.ctx.measureText(myText);

        this.ctx.fillText(myText, (stageWidth - fontInfo.width) / 2, (fontInfo.actualBoundingBoxAscent + fontInfo.actualBoundingBoxDescent) + (stageHeight - fontSize) / 2);

        return this.dotPos(density, stageWidth, stageHeight);
    }

    dotPos(density, stageWidth, stageHeight){
        const imageData = this.ctx.getImageData(0, 0, stageWidth, stageHeight).data;

        const particle = [];
        let width = 0;
        let pixel;
        let i = 0;

        for (let height = 0; height < stageHeight; height += density) {
            ++i;
            width = 0;
            const slide = ( i % 2 ) == 0;
            if (slide == 1) {
                width += 6;
            }
            for (width; width < stageWidth; width += density) {
                pixel = imageData[(width + ( height * stageWidth )) * 4 -1];

                if ( pixel != 0 && width > 0 && width < stageWidth && height > 0 && height < stageHeight) {
                    particle.push({
                        x: width,
                        y: height,
                    })
                }
            }
        }
        return particle;
    }
}